<template>
    <div>
      <el-header style="position: relative">
        <el-tabs v-model="activeName" type="card" @tab-click="handleClick">

          <el-tab-pane label="全部会议" name="first">
            <div class="meeting-list">
              <div v-for="meeting in meetingList" :key="meeting.meetingId" class="meeting-item">
                <el-card>
                  <el-form :model="meeting">
                    <el-form-item label="title">
                      <span>{{meeting.title}}</span>
                    </el-form-item>
                    <el-form-item label="ownerName">
                      <span>{{meeting.ownerName}}</span>
                    </el-form-item>
                    <el-form-item label="startTime">
                      <span>{{meeting.startTime}}</span>
                    </el-form-item>
                    <el-form-item label="endTime">
                      <span>{{meeting.endTime}}</span>
                    </el-form-item>
                    <el-form-item label="note">
                      <span>{{meeting.note}}</span>
                    </el-form-item>
                  </el-form>
                </el-card>
              </div>
            </div>
          </el-tab-pane>
          <el-tab-pane label="待办列表" name="todo">待办列表</el-tab-pane>
          <el-tab-pane label="我创建的" name="mine">我创建的</el-tab-pane>
          <el-tab-pane label="待办列表" name="history">历史会议</el-tab-pane>
        </el-tabs>
        <el-input placeholder="查找会议" v-model="meetingInput" prefix-icon="el-icon-search" style="width: 300px;position:absolute;top:2px;left:450px">
          <el-button slot="append" @click="searchMeeting">搜索</el-button>
        </el-input>
      </el-header>


    </div>
</template>

<script>
    export default {
        name: "Meeting",
      data(){
          return{
            meetingList:[],
            activeName: 'first',
            meetingInput:''
          }
      },
      methods:{
       async searchMeeting(){
         const { data: res } = await this.$http
            .post(`meeting/search/`,{
              linkNum:this.meetingInput
            })
            .catch(function(error) {
              console.log(error);
            });

         if (res.result == 1) {
           this.$message({
             message: "查询会议成功",
             type: "success"
           });
           console.log(res)

         } else {
           this.$message({
             message: res.message,
             type: "error"
           });
         }
        },
        async getMeetingByName(name){
          const {data:res} = await this.$http.get(`meeting/${name}`).catch(
            error => console.log(error)
          )
          if (res.result == 1) {
            this.$message({
              message: "获取会议列表成功",
              type: "success"
            });
            console.log(res)

          } else {
            this.$message({
              message: res.message,
              type: "error"
            });
          }
        },
        handleClick(tab, event) {
         if(tab.name!='first'){
           this.getMeetingByName(tab.name)
         }
        },
        async getMeeting(project) {
          const { data: res } = await this.$http
            .get(`project/${project}/getMeeting`)
            .catch(function(error) {
              console.log(error);
            });
          if (res.result == 1) {
            this.$message({
              message: "获取会议列表",
              type: "success"
            });
           this.meetingList= res.data

          } else {
            this.$message({
              message: res.message,
              type: "error"
            });
          }
        },
      },
      mounted() {
        this.token = window.sessionStorage.getItem('token')
        this.projectId = this.$route.params.id;
        console.log(this.projectId);
        this.getMeeting(this.projectId);
      }
    }
</script>

<style scoped>
.meeting-list{
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 25%);
  grid-template-rows: repeat(auto-fill, 200px);
}

</style>



