<template>
  <div class="file-container">
    <el-row class="search-header">
      <el-col :span="3">
        <span class="project-name">{{ projectName }}</span>
      </el-col>
      <el-col :span="2">
        <span>3项,73.84MB</span>
      </el-col>
      <el-col :offset="1" :span="4">
        <el-input
          prefix-icon="el-icon-search"
          class="project-search"
          placeholder="搜索文件"
        >
        </el-input>
      </el-col>
      <el-col :span="3" style="display: flex">
        <el-select v-model="value" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-col>
      <el-col :offset="1" :span="1">
        <el-button type="text" @click="showCreateMeetingVisible = true"
          >创建会议</el-button
        >
      </el-col>
      <el-col :offset="1" :span="7">
        <div class="user-list">
          <div
            class="user-item"
            v-for="(user, index) in userList"
            :key="index"
            :style="`zIndex:${10 - index}`"
            @click="showUserList"
          >
            <el-tooltip
              class="item"
              effect="light"
              :content="user.userName"
              placement="bottom  "
            >
              <el-avatar :src="user.avatar"></el-avatar>
            </el-tooltip>
          </div>
          <div class="user-item user-num" @click="showUserList">
            {{ userList.length }}
          </div>
        </div>
      </el-col>
      <el-col :span="1">
        <el-tooltip effect="light" content="邀请成员" placement="bottom">
          <div class="user-add" @click="inviteUser">
            <i class="el-icon-circle-plus" style="font-size: 38px;"></i>
          </div>
        </el-tooltip>
      </el-col>
    </el-row>
    <div class="file-upload" v-if="showList">
      <ele-upload-video
        :projectId="projectId"
        :fileSize="20"
        @error="handleUploadError"
        :responseFn="handleResponse"
        :httpRequest="request"
        style="margin: 50px"
        action="http://188.131.227.20:1314/api/video/"
        v-model="video"
      />
    </div>
    <div v-else class="video-list">
      <upload-video class="video-item" :projectId="projectId"> </upload-video>

      <el-card
        v-for="(video, index) in videoList"
        :key="index"
        class="video-item"
      >
        <el-image
          class="video-cover"
          style="width: 270px; height: 150px"
          :src="video.cover[0]"
          :preview-src-list="srcList"
          fit="scale-down"
          @click="goMeeting()"
        >
        </el-image>
        <div class="video-description">{{ video.videoName }}</div>
        <span style="font-size: 9px;color:#333">{{
          video.createDate | dateFormat
        }}</span>
        <el-popover placement="bottom" trigger="click">
          <div style="display: flex;flex-direction: column;">
            <el-button type="text"></el-button>
            <el-button type="text" icon="el-icon-delete">分享</el-button>
            <el-button type="text" icon="el-icon-delete">删除</el-button>
          </div>

          <i class="el-icon-more require-more" slot="reference"></i>
        </el-popover>
      </el-card>
    </div>
    <el-dialog
      :title="projectName"
      :visible.sync="showUserListVisible"
      width="30%"
    >
      <div class="show-user-list">
        <el-row v-for="user in userList" :key="user.userId">
          <el-col :offset="2" :span="15" class="flex-center">
            <el-avatar
              style="width: 35px;height: 35px;object-fit: scale-down"
              :src="user.avatar"
            ></el-avatar>
            <span style="margin-left: 5px">{{ user.userName }}</span>
          </el-col>
          <el-col :span="4" class="flex-center">
            <div>{{ user.title }}</div>
          </el-col>
          <el-col :span="2" class="flex-center">
            <el-popover placement="right" width="100" trigger="click">
              <el-button-group style="display: flex;flex-direction: column">
                <el-button type="text" @click.native="removeUserById(user.userId)"
                  >移除成员</el-button
                >
                <el-button type="text">设为管理员</el-button>
              </el-button-group>

              <el-button
                slot="reference"
                type="text"
                icon="el-icon-more"
                style="transform:rotate(90deg);margin-left: 15px"
              ></el-button>
            </el-popover>
          </el-col>
        </el-row>
      </div>
      <div
        style="height: 32px;line-height: 32px;border: 1px solid #e0e0e0;border-radius: 10px;display: flex;justify-content: center;margin-top: 5px;cursor: pointer"
        @click="inviteUser"
      >
        邀请更多成员
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showUserListVisible = false">取 消</el-button>
        <el-button type="primary" @click="showUserListVisible = false"
          >确 定</el-button
        >
      </span>
    </el-dialog>
    <el-dialog
      title="邀请用户"
      :visible.sync="showVisitUserVisible"
      width="30%"
      :before-close="VisitUserClose"
    >
      <el-form>
        <el-row>
        <el-input
          placeholder="请输入用户信息"
          v-model="inviteInput"
          prefix-icon="el-icon-search"
        >
          <template slot="append"
            ><el-button @click="searchUser">搜索</el-button></template
          >
        </el-input>
        </el-row>
        <div v-if="visitUser.length === 0">未找到指定用户</div>
        <div class="visit-list">
          <el-row
            type="flex"
            class="visit-user"
            v-for="user in visitUser"
            :key="user.userId"
          >
            <el-col :span="4"  class="visit-user-col">
              <el-avatar
                style="width: 35px;height: 35px;object-fit: scale-down"
                :src="user.avatar"
              ></el-avatar
            ></el-col>
            <el-col :span="4"  class="visit-user-col">
              <span>{{ user.username }}</span></el-col
            >
            <el-col :offset="2" :span="8"  class="visit-user-col">
              <span>{{ user.mobileNum }}</span></el-col
            >
            <el-col :offset="3" :span="3"  class="visit-user-col">
              <el-button @click="inviteUserbyId(user.userId)"
                >邀请</el-button
              ></el-col
            >
          </el-row>
        </div>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showVisitUserVisible = false">取 消</el-button>
        <el-button type="primary" @click="showUserListVisible = false"
          >邀请</el-button
        >
      </span>
    </el-dialog>
    <el-dialog
      title="创建会议"
      :visible.sync="showCreateMeetingVisible"
      width="40%"
      :before-close="CreateMeetingClose"
    >
      <el-form :model="meeting">
        <el-form-item label="title">
          <el-input v-model="meeting.title"></el-input>
        </el-form-item>
        <el-form-item label="belongTo projectId">
          <el-input v-model="meeting.belongTo" disabled></el-input>
        </el-form-item>
        <el-form-item label="startTime">
          <el-input v-model="meeting.startTime"></el-input>
        </el-form-item>
        <el-form-item label="endTime">
          <el-input v-model="meeting.endTime"></el-input>
        </el-form-item>
        <el-form-item label="note">
          <el-input v-model="meeting.note"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="createMeeting">创建会议</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import UploadVideo from "./UploadVideo";
import EleUploadVideo from "./EleUploadVideo";
import axios from "axios";
export default {
  name: "File",
  components: {
    UploadVideo,
    EleUploadVideo
  },
  props: {
    projectName: String
  },
  data() {
    return {
      dataObj: new FormData(),
      headerobj: {
        Authorization: "Bearer " + window.sessionStorage.getItem("token"),
        "Content-Type": "multipart/form-data"
      },
      video: "",
      imageUrl: "",
      srcList: [],
      userList: [],
      videoList: [],
      visitUser: [],
      meeting: {
        title: "",
        belongTo: "",
        startTime: "",
        endTime: "",
        note: ""
      },
      options: [
        {
          value: "选项1",
          label: "最近发布"
        },
        {
          value: "选项2",
          label: "按类型"
        },
        {
          value: "选项3",
          label: "时间"
        },
        {
          value: "选项4",
          label: "大小排序"
        },
        {
          value: "选项5",
          label: "北京烤鸭"
        }
      ],
      value: "",

      showUserListVisible: false,
      showVisitUserVisible: false,
      showCreateMeetingVisible: false,
      projectId: "",
      inviteInput: "",
      video: null
    };
  },
  methods: {
    CreateMeetingClose() {
      this.meeting = {};
    },
    async createMeeting() {
      const { data: res } = await this.$http.post("meeting/", this.meeting);
      if (res.result === 1) {
        this.$message({
          message: "成功创建会议",
          type: "success"
        });
      } else {
        this.$message({
          message: res.message,
          type: "error"
        });
      }
      this.showCreateMeetingVisible = false;
    },
    async request(file) {
      this.dataObj.append("videoName", file.name);
      this.dataObj.append("description", file.type);
      this.dataObj.append("permission", "0");
      this.dataObj.append("uploadToProject", this.projectId);
      this.dataObj.append("video", this.video);

      var self = this;
      const { data } = await axios.post(
        "http://188.131.227.20:1314/api/video/",
        this.dataObj,
        {
          headers: self.headerobj
        }
      );
    },

    async getProjectInfo(project) {
      const { data: res } = await this.$http
        .get(`project/${project}/userAndVideo`)
        .catch(function(error) {
          console.log(error);
        });
      if (res.result == 1) {
        this.$message({
          message: "获取项目视频列表和用户列表成功",
          type: "success"
        });

        this.videoList = res.data.videoList;
        this.userList = res.data.userList;
      } else {
        this.$message({
          message: res.message,
          type: "error"
        });
      }
    },
    async searchUser() {
      const { data: res } = await this.$http
        .get("userlist/", {
          params: {
            data: this.inviteInput
          }
        })
        .catch(function(error) {
          console.log(error);
        });
      if (res.result == 1) {
        this.$message({
          message: "获取用户列表成功",
          type: "success"
        });
        this.visitUser = res.data;
      } else {
        this.$message({
          message: res.message,
          type: "error"
        });
      }
    },
    async inviteUserbyId(id) {
      const { data: res } = await this.$http
        .post(`project/${this.projectId}/inviteUser/`, {
          userId: id,
          word: "我喜欢你"
        })
        .catch(function(error) {
          console.log(error);
        });
      if (res.result == 1) {
        this.$message({
          message: "邀请成功",
          type: "success"
        });
      } else {
        this.$message({
          message: res.message,
          type: "error"
        });
      }
    },
    async removeUserById(id) {
      const { data: res } = await this.$http
        .delete(`project/${this.projectId}/removeUser`, {
          params: {
            userId: id
          }
        })
        .catch(function(error) {
          console.log(error);
        });
      if (res.result == 1) {
        this.$message({
          message: "邀请成功",
          type: "success"
        });
      } else {
        this.$message({
          message: res.message,
          type: "error"
        });
      }
    },
    showUserList() {
      this.showUserListVisible = true;
    },
    inviteUser() {
      this.showUserListVisible = false;
      this.showVisitUserVisible = true;
      this.searchUser();
    },

    VisitUserClose(done) {
      this.inviteInput = "";
    },
    handleUploadError(error) {
      console.log("error", error);
    },
    handleResponse(response) {}
  },
  computed: {
    showList() {
      return this.videoList.length === 0;
    }
  },
  mounted() {
    this.token = window.sessionStorage.getItem("token");
    this.projectId = this.$route.params.id;
    this.meeting.belongTo = this.projectId;
    console.log(this.projectId);
    this.getProjectInfo(this.projectId);
  },
  updated() {
    if (this.projectId != this.$route.params.id) {
      this.projectId = this.$route.params.id;
      console.log(this.projectId);
      this.getProjectInfo(this.projectId);
    }
  }
};
</script>

<style scoped>
.file-container {
  position: relative;
  height: 100%;
}
.search-header {
  height: 38px;
  line-height: 38px;
  border-bottom: 1px solid #c0c0c0;
}
.project-search {
  width: 180px;
}
.user-list {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.user-item {
  margin-right: -10px;
  height: 35px;
  width: 35px;
}
.user-num {
  height: 35px;
  width: 35px;
  border-radius: 50%;
  background-color: #c0c0c0;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #303030;
}
.el-avatar {
  height: 100%;
  width: 100%;
}
.user-add {
  margin-left: 10px;
  color: #c0c0c0;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}

.video-list {
  width: 100%;

  display: grid;
  grid-template-columns: repeat(4, 25%);
  grid-template-rows: repeat(auto-fill, 250px);
}
.video-item {
  margin: 10px;
  position: relative;
}

.file-upload {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 200px;
  height: 200px;
  transform: translate(-50%, -50%);
  border: 1px dashed #8c939d;
  display: flex;
  justify-content: center;
  align-items: center;
}
.video-list {
  display: grid;
  grid-template-columns: repeat(4, 25%);
  grid-template-rows: repeat(auto-fill, 250px);
}
.video-item {
  margin: 10px;
}

.project-name {
  background: linear-gradient(to right, red, blue);
  -webkit-background-clip: text;
  color: transparent;
  -webkit-text-fill-color: transparent;
  text-fill-color: transparent;
}
.show-user-list {
  display: flex;
  flex-direction: column;
}
.flex-center {
  display: flex;
  height: 40px;
  line-height: 40px;
}
.video-cover {
  cursor: pointer;

  /*animation:mymove 5s infinite;*/
  /*animation-direction:alternate;*/
  /*animation-timing-function: ease-in-out;*/
}
.el-card:hover {
  transform: scale(1.1);
}
/*@keyframes mymove*/
/*{*/
/*  0%{*/
/*    transform: scale(1);  !*开始为原始大小*!*/
/*  }*/
/*  25%{*/
/*    transform: scale(1.1); !*放大1.1倍*!*/
/*  }*/
/*  50%{*/
/*    transform: scale(1);*/
/*  }*/
/*  75%{*/
/*    transform: scale(1.1);*/
/*  }*/
.require-more {
  transform: rotate(90deg);
  position: absolute;
  right: 20px;
  cursor: pointer;
}
  .visit-user-col{
    display: flex;
    justify-content: center;
    align-items: center;
  }
</style>



